import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CardsService } from 'src/app/commonBussinessSRC/psServices/Cards/cards.service';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IOptionsPsOptionCardExposed } from './ps-option-card.component.interface';



@Component({
  selector: 'ps-option-card',
  templateUrl: './ps-option-card.component.html',
  styleUrls: ['./ps-option-card.component.scss'],
})
export class PsOptionCardComponent extends PsTemplateListPage implements OnInit {
  @Input() options: IOptionsPsOptionCardExposed;
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  private showSingleAccount = false;
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  public cardLabelOptions: IOptionsPsLabel = {
    labelKey: 'card_list_not_available'
  };
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onClickCard = new EventEmitter<any>();

  constructor(private crdsSrvce: CardsService, private cdRef?: ChangeDetectorRef
  ) {
    super();
  }

  ngOnInit() {
    super.init();
    this.populateHeaderValueMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options.cardAction === null || this.options.cardAction === undefined) {
      this.options.cardAction = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
    if (this.options && this.options.noCardsLabel) {
      this.cardLabelOptions.labelKey = this.options.noCardsLabel;
    }
  }

  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  //edited by Marina to avoid action links continous reload caused by ngOnChanges() in ps-container-lookup-option component on having empty objects in lookupDetails.actionDetailsOptions[] 
  private prepareLookUp(card: any): any {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    lookupDetails.currencyDescription = 'currencyBriefName';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.balanceMapping = this.balanceMapping();
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.translateSubTitle = this.options.translate;
    lookupDetails.matCardCssSelectors = card.matCardCssSelectors;
    lookupDetails.showInitialCardValues = 4; // added by Marina TP:#1578052 to include primary/settlement account number in the fields visible in debit/credit card lookup
    lookupDetails.fcName = this.options?.fcName; //Add by Mohanad AZDB-5369-22072024 to fix issue of overiding fcnames when lookup in same screen
    if (!this.options.cardAction) {
      lookupDetails.editActionUrl = PsCommonBusinessSettings.serviceUrl.personalizationNicknameEndPoint;
      lookupDetails.requestMap = this.requestMap;
      lookupDetails.editRequestMap = this.editRequestMap;
      lookupDetails.pageData = {
        operID: CommonBussinessConstant.CARD_STATEMENT,
        title: 'card_report_key'
      };
      lookupDetails.actionDetailsOptions = [
        {
          id: 'pin_change',
          label: 'pin_change_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'pin-change',
              labelOptions: {
                labelKey: 'pin_change_key'
              }
            },
            route: 'card-pin-request',
            pageOptions: {
              operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.CHANGE_CREDIT_CARD_PIN : CommonBussinessConstant.CHANGE_DEBIT_CARD_PIN,
              iconName: 'pin-change',
              title: 'card_pin_change_key',
            },
            navigationOptions: {
              queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_PIN_CHANGE },
            },
          }
        }, {
          id: 'pin_reset',
          label: 'pin_reset_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'pin-reset',
              labelOptions: {
                labelKey: 'pin_reset_key'
              }
            },
            route: 'reset-pin-request',
            pageOptions: {
              operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.RESET_CREDIT_CARD_PIN : CommonBussinessConstant.RESET_DEBIT_CARD_PIN,
              iconName: 'pin-reset',
              title: 'card_pin_reset_key',
            },
            navigationOptions: {
              queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_PIN_RESET },
            },
          }
        }, {
          id: 'deactivate',
          labelOptions: {
            labelKey: 'deactivate_key'
          },
          buttonIcon: 'deactivate',
          actionUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
          label: 'deactivate_key',
          iconName: 'square-outline',
          actionType: 'Submit',
          operId: this.options.type == CardsService.DEBIT_CARD_TYPE_NAME ?
            CommonBussinessConstant.DEACTIVATE_DEBIT_CARD
            : this.options.type == CardsService.CREDIT_CARD_TYPE_NAME ?
              CommonBussinessConstant.DEACTIVATE_CREDIT_CARD : CommonBussinessConstant.DEACTIVATE_PREPAID_CARD
          ,
          postCallFunction: {
            func() {
              return new Promise<any>((resolve, reject) => {
                resolve(this.executionClass.reloadList());
              });
            },
            executionClass: this,
            params: []
          },
        }
      ];
      const renewOption = {
        id: 'renew_card',
        label: 'renew_card_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'renew-card',
            labelOptions: {
              labelKey: 'renew_card_key'
            }
          },
          route: 'card-management',
          pageOptions: {
            operId: CommonBussinessConstant.RENEW_DEBIT_CARD,
            iconName: 'renew-card',
            title: 'renew_card_key',
          },
          navigationOptions: {
            queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_RENEW },
          },
        }
      };
      const settleOption = {
        id: 'settle_card',
        label: 'settle_card_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'settle',
            labelOptions: {
              labelKey: 'settle_card_key'
            }
          },
          route: 'credit-card-settlement',
          pageOptions: {
            operId: CommonBussinessConstant.CREDIT_CARD_SETTLEMENT,
            iconName: 'settle',
            title: 'settle_card_key',
          },
          preCallFunction: {
            func() {
              return new Promise<any>((resolve, reject) => {
                resolve(this.executionClass.preparesettleScreenData(lookupDetails.actionDetailsOptions.find(card => card.id == 'settle_card').actionHyperlink));
              });
            },
            executionClass: this,
            params: []
          },
          navigationOptions: {
            queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_SETTLE },
          },
        }

      }
      const requestPrepaidCardOption = {
        id: 'prepaid_card_request',
        label: 'prepaid_card_request_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'new-card-request',
            labelOptions: {
              labelKey: 'prepaid_card_request_key'
            }
          },
          route: 'prepaid-card-request',
          pageOptions: {
            operId: CommonBussinessConstant.PREPAID_CARD_REQ,
            iconName: 'new-card-request',
            title: 'prepaid_card_request_key',
          },
          navigationOptions: {
            queryParams: {
              referenceCard: card.cardNumber,
              referenceCard_lookupKey: card.cardNumber

            },
          }
        },
      }
      const rechargeOption = {
        id: 'recharge_card',
        label: 'recharge_card_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'transfers',
            labelOptions: {
              labelKey: 'recharge_card_key'
            }
          },
          route: 'prepaid-card-recharge',
          pageOptions: {
            operId: CommonBussinessConstant.PREPAID_CARD_RECHARGE,
            iconName: 'transfers',
            title: 'prepaid_card_recharge_key',
          },

          navigationOptions: {
            queryParams: {
              toCard: card.cardNumber,
              toCard_lookupKey: card.cardNumber

            },
          }
        }

      }
      if (card.wdLimitOverride === '1' || (card.posLimitOverride === '1')) {
        lookupDetails.actionDetailsOptions.push({
          id: 'limit_update',
          label: 'limit_update_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'sync',
              labelOptions: {
                labelKey: 'limit_update_key'
              }
            },
            route: 'card-management',
            pageOptions: {
              operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.UPDATE_CREDIT_CARD_LIMIT : CommonBussinessConstant.UPDATE_DEBIT_CARD_LIMIT,
              iconName: 'sync',
              title: 'card_limit_update_key',
            },
            navigationOptions: {
              queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_LIMIT_UPDATE, cardType: this.options.type },
            },
          }
        },
          {
            id: 'upgrade_card',
            label: 'upgrade_card_key',
            actionHyperlink: {
              group: this.options.group,
              iconOptions: {
                iconName: 'upgrade-card',
                labelOptions: {
                  labelKey: 'upgrade_card_key'
                }
              },
              route: 'upgrade-card',
              pageOptions: {
                operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.UPGRADE_CREDIT_CARD : CommonBussinessConstant.UPGRADE_DEBIT_CARD,
                iconName: 'upgrade-card',
                title: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? 'upgrade_credit_card_request_key' : 'upgrade_debit_card_request_key',
              },
              navigationOptions: {
                queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_UPGRADE },
              },
            }
          }
        );
      }
      if (card.status === CommonBussinessConstant.CARD_STATUS_ACTIVE || card.statusCode == CommonBussinessConstant.CARD_STATUS_ACTIVE_CODE) {
        lookupDetails.actionDetailsOptions.push({
          id: 'replace_card',
          label: 'replace_card_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'edit1',
              labelOptions: {
                labelKey: 'replace_card_key'
              }
            },
            route: 'card-management',
            pageOptions: {
              operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.REPLACE_CREDIT_CARD : CommonBussinessConstant.REPLACE_DEBIT_CARD,
              iconName: 'edit1',
              title: 'replace_card_key',
            },
            navigationOptions: {
              queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_REPLACE },
            },
          }
        }

        )
      }
      if (card.status === CommonBussinessConstant.CARD_STATUS_BLOCKED || card.statusCode === CommonBussinessConstant.CARD_STATUS_BLOCKED_CODE) {
        lookupDetails.actionDetailsOptions.push(
          {
            id: 'unblock',
            label: 'unblock_key',
            actionHyperlink: {
              group: this.options.group,
              iconOptions: {
                iconName: 'activate-card',
                labelOptions: {
                  labelKey: 'unblock_key'
                }
              },
              route: 'card-management',
              pageOptions: {
                operId: this.options.type === CardsService.CREDIT_CARD_TYPE_NAME ? CommonBussinessConstant.UNBLOCK_CREDIT_CARD : CommonBussinessConstant.UNBLOCK_DEBIT_CARD,
                iconName: 'activate-card',
                title: 'unblock_card_key',
              },
              navigationOptions: {
                queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_UNBLOCK },
              },
            }
          }
        )
      }
      if (card.status === CommonBussinessConstant.CARD_STATUS_ACTIVE || card.statusCode == CommonBussinessConstant.CARD_STATUS_ACTIVE_CODE) {
        lookupDetails.actionDetailsOptions.push(
          {
            id: 'block',
            label: 'block_key',
            actionHyperlink: {
              group: this.options.group,
              iconOptions: {
                iconName: 'block-card',
                labelOptions: {
                  labelKey: 'block_key'
                }
              },
              route: 'card-management',
              pageOptions: {
                operId: CommonBussinessConstant.BLOCK_DEBIT_CARD,
                iconName: 'block-card',
                title: 'block_card_key',
              },
              navigationOptions: {
                queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_BLOCK },
              },
            }
          }
        )
      }
      if (card.primarySupplementary === CommonBussinessConstant.CARD_PRIMARY_TYPE && card.allowSuppCard === '1') {
        lookupDetails.actionDetailsOptions.push(
          {
            id: 'add_on_card',
            label: 'add_on_card_key',
            actionHyperlink: {
              group: this.options.group,
              iconOptions: {
                iconName: 'edit1',
                labelOptions: {
                  labelKey: 'add_on_card_key' // needs to be registered
                }
              },
              route: 'supplementary-card-request',
              pageOptions: {
                operId: this.options.type == CardsService.CREDIT_CARD_TYPE_NAME ?
                  CommonBussinessConstant.SUPPLEMENTARY_CREDIT_CARD_REQ_OPER_ID : CommonBussinessConstant.SUPPLEMENTARY_DEBIT_CARD_REQ_OPER_ID,
                iconName: 'card-input',
                title: this.options.type == CardsService.CREDIT_CARD_TYPE_NAME ? 'supplementary_credit_card_request_key' : 'supplementary_debit_card_request_key',
              },
              navigationOptions: {
                queryParams: { action: CommonBussinessConstant.CARD_ACTION_TYPE_ADD_ON },
              },
            }
          }
        )
      }

      if (this.options.type === CardsService.DEBIT_CARD_TYPE_NAME) {
        if (this.commonProv.hasAccessOnOperation(CommonBussinessConstant.PREPAID_CARD_REQ))
          lookupDetails.actionDetailsOptions.push(requestPrepaidCardOption)
      }
      else if (this.options.type === CardsService.CREDIT_CARD_TYPE_NAME) {
        if (card.statusCode == CommonBussinessConstant.CARD_STATUS_ACTIVE_CODE) {
          lookupDetails.actionDetailsOptions.push(settleOption);
        }
      }
      else {
        lookupDetails.actionDetailsOptions.push(rechargeOption);
      }

      const dateToBeCheckOut = new Date(card.expiryDate);
      const expiryDate = new Date(card.expiryDate);
      const today = new Date();
      if (card.maxRenewPeriod) {
        dateToBeCheckOut.setMonth(dateToBeCheckOut.getMonth() + card.maxRenewPeriod);
      }

      if (today > expiryDate && card.allowRenew === '1') {
        if (Number(card.maxRenewPeriod) > 0 && today <= dateToBeCheckOut) {
          lookupDetails.actionDetailsOptions.push(renewOption);
        } else {
          // lookupDetails.actionDetailsOptions.push(renewOption);
        }
      }
      lookupDetails.statementOptions = {
        buttonIcon: 'list-box',
        redirectUrl: 'card-statement-report'
      };
    } else {
      lookupDetails.pageData = {};
      lookupDetails.actionDetailsOptions = [];
      lookupDetails.statementOptions = {};
    }
	//AZDB-8846 AIBIRQI250160 - 204612 - when submit a request added by dynamic action button, theres no success or failure msg showing
      lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }

  @Output() reloadFct = new EventEmitter<string>();
  reloadList() {
    this.reloadFct.emit();
  }
  private populateHeaderValueMap() {
    let expiryDateMap: IMapKeyValue = {};
    let holderNameMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let currencyMap: IMapKeyValue = {};
    let primaryAccountMap: IMapKeyValue = {};
    let settlementAccountMap: IMapKeyValue = {};
    let withdrawLimitMap: IMapKeyValue = {};
    let remainingWithlimitMap: IMapKeyValue = {};
    let posLimitsMap: IMapKeyValue = {};
    let remainingPosLimitMap: IMapKeyValue = {};
    let paymentPercentMap: IMapKeyValue = {};
    let billingCycleMap: IMapKeyValue = {};
    let loyaltyPtsMap: IMapKeyValue = {};
    let availableBalanceMap: IMapKeyValue = {};
    let outStandingMap: IMapKeyValue = {};
    let primarySupplementaryMap: IMapKeyValue = {};
    let primaryCardMap: IMapKeyValue = {};


    expiryDateMap = {
      key: 'expiryDate',
      value: ''
    };

    holderNameMap = {
      key: 'ownerName',
      value: ''
    };

    currencyMap = {
      key: 'currencyBriefNameEnglish',
      value: ''
    };

    primaryAccountMap = {
      key: 'primaryAccount',
      value: ''
    };

    settlementAccountMap = {
      key: 'primaryAccount',
      value: ''
    };

    withdrawLimitMap = {
      key: 'currentWdLimit',
      value: ''
    };

    remainingWithlimitMap = {
      key: 'remainingWithdrawLimit',
      value: ''
    };

    posLimitsMap = {
      key: 'posLimitAmount',
      value: ''
    };

    remainingPosLimitMap = {
      key: 'remainingPosLimit',
      value: ''
    };

    paymentPercentMap = {
      key: 'paymentPercentage',
      value: ''
    };

    billingCycleMap = {
      key: 'billingCycle',
      value: ''
    };

    loyaltyPtsMap = {
      key: 'loyaltyPts',
      value: ''
    };

    availableBalanceMap = {
      key: 'remainingBalance',
      value: ''
    };
    outStandingMap = {
      key: 'outStanding',
      value: ''
    };

    primarySupplementaryMap = {
      key: 'primarySupplementary',
      value: ''
    };

    primaryCardMap = {
      key: 'primaryCard',
      value: '',
      showMappingOnNoValue: true
    };

    headerTitleMap = {
      key: 'title',
      value: 'title',
      isEdit: false,
      formGroupMap: {
        placeHolder: 'enter_card_type',
        fcName: 'cardType',
      }
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'ownerName',
      isEdit: true,
      formGroupMap: {
        placeHolder: 'enter_cardName',
        fcName: 'cardName',
      }
    };

    // key here will be used as label in look up details component
    //this.labelValuesMap.set('card_number_key', cardNumberMap); --- Marina TP:#1242912 card number is added to title 
    this.labelValuesMap.set('primary_supplementary_key', primarySupplementaryMap);
    this.labelValuesMap.set('primary_card_key', primaryCardMap);
    this.labelValuesMap.set('currency_key', currencyMap);
    //this.labelValuesMap.set('name_on_card_key', holderNameMap); ---Marina TP:#1578094 as confirmed by PM nameOnCard should be removed from card body as it is already available in sublitlte 

    this.requestMap.set('nickNameNumber', 'key');
    this.requestMap.set('oldNickName', 'ownerName');
    this.editRequestMap.set('newNickName', 'ownerName');
    this.editRequestMap.set('type', 'C');

    if (this.options.type === CardsService.CREDIT_CARD_TYPE_NAME) {
      this.labelValuesMap.set('settlement_account_key', settlementAccountMap);
      this.labelValuesMap.set('withdrawal_limit_key', withdrawLimitMap);
      this.labelValuesMap.set('remaining_withdrawal_limit_key', remainingWithlimitMap);
      this.labelValuesMap.set('pos_limit_key', posLimitsMap);
      this.labelValuesMap.set('remaining_pos_limit_key', remainingPosLimitMap);
      this.labelValuesMap.set('payment_percentage_key', paymentPercentMap);
      this.labelValuesMap.set('billing_cycle_key', billingCycleMap);
      this.labelValuesMap.set('loyalty_points_key', loyaltyPtsMap);
      this.labelValuesMap.set('available_balance_key', availableBalanceMap);
      this.labelValuesMap.set('outstanding_balance_key', outStandingMap);
    } else {
      this.labelValuesMap.set('primary_account_key', primaryAccountMap);
    }
    this.labelValuesMap.set('expiry_date_key', expiryDateMap);

    // will be used as header to template card in look up details component
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);

  }

  updateCard(card: any) {
    if (card && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === card);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }

  private balanceMapping(): any[] {
    const balanceMappingList = [
      {
        key: 'outstanding_balance_key',
        value: 'outstandingBalance'
      },
      {
        key: 'available_balance_key',
        value: 'remainingBalance'
      }
    ];

    return balanceMappingList;
  }

  // eslint-disable-next-line @angular-eslint/use-lifecycle-interface
  ngAfterViewChecked() {
    this.cdRef.markForCheck();
  }

  preparesettleScreenData(actionCard: IOptionsPsActionHyperlink) {
    const creditCardSettlementSec = this.commonProv.session.getValueOf(ConstantCommon.USERINFO).businessProfiles.find((i) => i.operVO.OPER_ID === CommonBussinessConstant.CARDS_SEC_OPER_ID)?.children.find(c => c.operVO.OPER_ID === CommonBussinessConstant.CREDIT_CARD_SETTLEMENT_SEC_OPER_ID);
    const creditCardSettleScreen = creditCardSettlementSec?.children.find(c => c.operVO.OPER_ID === CommonBussinessConstant.CREDIT_CARD_SETTLEMENT);
    actionCard.navigationOptions.queryParams.ScreenVO = {
      creditCard: actionCard.navigationOptions.queryParams.cardNumber,
      creditCard_lookupKey: actionCard.navigationOptions.queryParams.cardNumber,
      defaultSettlementAccount: creditCardSettleScreen?.operAppChnlVO?.PARAM ? JSON.parse(creditCardSettleScreen.operAppChnlVO.PARAM)?.defaultSettlementAccount : true
    };
  }


}
